.container-login{
    padding: 4rem 0 0 20%;
    background: #eee;
    min-height: 100vh;
}

#react-app{
    padding-bottom: 1rem;
}

.container-login a{
    font-size: 3rem;
}

button{
    width: 10rem;
}

button span{
    color:#000;
}
.container{
    width:100%;
    padding:0!important;
    margin:0!important;
    max-width: 100%!important;
    min-height: calc(100vh - 4rem);
    background: #999;
}

.fa{
    cursor: pointer;
}

.container img{
    display: block;
    margin: 0 auto;
}

.wrapper-top-header h1{
    padding-bottom: 2rem;
    font-size: 3rem;
}

.container audio{
    width: 100%;
    display:block;
    margin: 0 auto;
}

.container-current-top{
    padding-top: 2rem;
    padding-bottom: 5rem;
    display:flex;
    justify-content: center;
}

.wrapper-top-preview{
    width: 30rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container-favorite-genres{
    padding-bottom: 4rem;
    width:100%;
}

.progress{
    width: 25%;
    margin-top: 0.5rem;
}

.genres{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.fontawesome{
    width: 100px;
    height:100px;
    display:block;
}
.container-profile{
    width:100%;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #b3b3b3;
    min-height: calc(100vh - 4rem);
}

.container-profile img{
    padding: 3rem;
    width: 20rem;
    height:20rem;
    border-radius: 50%;
}
h6{
    margin:0;
    padding:0;
}

.search-container{
    background: #999;
    min-height: calc(100vh - 4rem);
}

input:focus::-webkit-input-placeholder{
    color: transparent;
}

input:focus::-moz-placeholder{
    color:transparent;
}

input:focus::-ms-input-placeholder{
    color:transparent;
}

.search-result{
    padding-top: 3rem;
}

.album-header{
    margin-left: 4rem;
    margin-right: 4rem;
    border-top: 1px black dotted;
    border-bottom: 1px black dotted;
}

.album-header:hover{
    cursor: pointer;
}

.text-center .form-control{
    width: 20rem!important;
    margin: 1rem auto;
    text-align:center;
    background: #fff!important;
    border: 2px #000 solid;
    color: #000;
}

.text-center .form-control:focus, 
.text-center  .form-control:hover{
    background: #bbb!important;
}

.btn{
    width: 20rem!important;
    color: black!important;
}

.search-result h6{
    width: 20rem;
}

.form-group{
    position: relative;
    width: 10rem;
    margin: 0 auto;
}

.form-group p{
    text-align: center;
    margin-bottom: 0;
}

.form-group .form-control{
    width: 100%;
}

.image-artwork{
    box-shadow: 0 0 20px #000;
    border-radius: 10px;
}

.image-artwork:hover{
    opacity: 0.7;
    cursor: pointer;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position:fixed;
    display: flex;
    background: #EFEFEF;
    overflow-y:auto;
    width: 80%;
    height: 90vh;
    min-height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border-radius: 20px;
}

.track-list-container{
    padding: 3rem 3rem 3rem 5rem;
}

.display-block {
    display: block!important;
}

.display-none {
    display: none;
}

.track-item{
    font-size: 1.5rem;
    color: #000;
    cursor: pointer;
}

.track-item:hover{
    list-style: none;
    position: relative;
    left: -1em;
}

.track-item i{
    display:none;
}

.track-item:hover i{
    display: inline-block;
    padding-right: 0.21em;
}

.track-artwork{
    align-self: flex-start;
    padding: 3rem;
}

.modal-close-button{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    color:red;
    font-size:2rem;
    background: #ddd;
    border: 1px black solid;
    cursor: pointer;
    float:right;
    position: absolute;
    right: 2rem;
    top: 1rem;
}

.modal-close-button:hover{
    background: #bbb;
    font-weight: bold;
}
body{
    overflow-x: hidden;
}

.top-track-container{
    background: #999;
    min-height: calc(100vh - 4rem);
}

.track-list:hover{
    list-style-type: none;
    position:relative;
    left: -1em;
    cursor:pointer;
}

.track-list i{
    display: none;
    opacity: 0.8;
}

.track-list:hover i{
    display: inline-block;
}
.top-artists-container{
    background: #999;
    min-height: calc(100vh - 4rem);
}
.navbar{
    height: 4rem;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

